import * as React from 'react';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import { useNavigate } from 'react-router-dom';
import ToolTip from '@mui/material/Tooltip';
import HomeIcon from '@mui/icons-material/Home';
import ForumIcon from '@mui/icons-material/Forum';
import FavoriteIcon from '@mui/icons-material/Favorite';
import SettingsIcon from '@mui/icons-material/Settings';

const OnlineBottomNav = () => {
    const navigate = useNavigate();

    const [value, setValue] = React.useState('home');

    const handleChange = (event: React.SyntheticEvent, newValue: string) => {
        setValue(newValue);
    };

    //login user
    const navigateHome = () => navigate('/home');
    const navigateChatMessages = () => navigate('/chat-messages');
    const navigateFavRooms = () => navigate('/favorite-room');
    const navigateSettings = () => navigate('/settings');
    return (

        <div className="w-full fixed bg-emerald-400/30 shadow-lg   bottom-0 left-0 md:hidden">
            <BottomNavigation sx={{ width: '100%' }} value={value} onChange={handleChange}>

                <BottomNavigationAction
                    label="Home"
                    value="home"
                    icon={<HomeIcon />}
                    onClick={navigateHome}
                />

                <BottomNavigationAction
                    label="ChatMessages"
                    value="chatmessages"
                    icon={<ForumIcon />}
                    onClick={navigateChatMessages}
                />

                <BottomNavigationAction
                    label="FavRoom"
                    value="favroom"
                    icon={<FavoriteIcon />}
                    onClick={navigateFavRooms}
                />

                <BottomNavigationAction
                    label="Settings"
                    value="settings"
                    icon={<SettingsIcon />}
                    onClick={navigateSettings}
                />
            </BottomNavigation>
        </div>
    );
}

export default OnlineBottomNav