import React from 'react'
import Navbar from './components/layouts/Navbar'
import Hero from './components/layouts/Hero'
import { Routes, Route } from 'react-router-dom';
import LoginPage from './components/pages/LoginPage';
import SignupPage from './components/pages/SignupPage';
import { createTheme, ThemeProvider } from '@mui/material/styles'
import OfflineBottomNav from './components/layouts/OfflineBottomNav';
import OnlineBottomNav from './components/layouts/OnlineBottomNav';

const theme = createTheme({
    palette: {
        secondary: {
            main: '#00c853'
        },
        primary: {
            main: '#2563eb'
        }
    }
})

const App = () => {
    const [login, setLogin] = React.useState<boolean>(false)
    return (
        <ThemeProvider theme={theme}>
            <div className='container-lg'>
                <Navbar />
                <main className='container-md mt-[20%] sm:mt-[10%]'>
                    <Routes>
                        <Route path="/" element={<Hero />} />
                        <Route path="/signup" element={<SignupPage />} />
                        <Route path="/login" element={<LoginPage />} />
                    </Routes>
                </main>

                {login ? <OnlineBottomNav /> : <OfflineBottomNav />}

            </div>
        </ThemeProvider>
    )
}

export default App