import React from 'react'
import Button from '@mui/material/Button';
import LoginIcon from '@mui/icons-material/Login';
import PersonAddAltIcon from '@mui/icons-material/PersonAddAlt';
import Stack from '@mui/material/Stack';
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import ForumIcon from '@mui/icons-material/Forum';
import FavoriteIcon from '@mui/icons-material/Favorite';
import SettingsIcon from '@mui/icons-material/Settings';

const Navbar = () => {

    const [login, setLogin] = React.useState<boolean>(true);

    return (
        <nav className="navbar fixed-top  p-3 drop-shadow-lg backdrop-blur-md bg-white/30">
            <div className="container-md justify-content-center items-center flex-no-wrap">
                <Link to="/" className="nav-brand  bg-clip-text text-transparent bg-gradient-to-br from-emerald-400 to-blue-600  font-bold no-underline text-4xl">Connect</Link>

                <ul className="hidden md:navbar-nav md:flex grow align-self-center justify-end space-x-9 mb-0">
                    {
                        !login ?
                            <>
                                <li className="nav-item">
                                    <Link to="signup" className='no-underline'>
                                        <Button variant="outlined" startIcon={<PersonAddAltIcon />}>Signup</ Button>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/login" className='no-underline'>
                                        <Button variant="contained" startIcon={<LoginIcon />}>Login</Button>
                                    </Link>
                                </li>

                            </>
                            :
                            <>
                                <li className="nav-item">
                                    <Link to="/home" className='no-underline'>
                                        <Button variant="text" startIcon={<HomeIcon />}>Home</ Button>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/room-messages" className='no-underline'>
                                        <Button variant="text" startIcon={<ForumIcon />}>Room messages</Button>
                                    </Link>
                                </li>

                                <li className="nav-item">
                                    <Link to="/favorite-room" className='no-underline'>
                                        <Button variant="text" startIcon={<FavoriteIcon />}>Fav Room</Button>
                                    </Link>
                                </li>

                                <li className="nav-item">
                                    <Link to="/favorite-room" className='no-underline'>
                                        <Button variant="text" startIcon={<SettingsIcon />}>Settings</Button>
                                    </Link>
                                </li>
                            </>
                    }
                </ul>
            </div>
        </nav>
    )
}


export default Navbar